/*nav styles*/
@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,500,600,700,800,300i,400i,500i,600i,700i,800i&display=swap'); 

body {  
    color:#212529;
    font-family:-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans","Liberation Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
    font-size:1rem;
    font-weight:400;
    line-height:1.5;
    text-align:left;
    word-spacing:1px;
  }  
* { 
    box-sizing: border-box;
} 

* { 
    box-sizing: border-box; 
    margin: 0;
} 

body { 
    background-color: #fff; 
    color: #212529; 
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans","Liberation Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji"; 
    font-size: 1rem; 
    font-weight: 400; 
    line-height: 1.5; 
    margin: 0; 
    text-align: left;
} 

body { 
    background-color: #f9f9f9; 
    color: #333; 
    font-family: "Open Sans"; 
    font-size: 14px;
} 

html { 
    -webkit-text-size-adjust: 100%; 
    -webkit-tap-highlight-color: rgba(0,0,0,0); 
    font-family: sans-serif; 
    line-height: 1.15;
} 

html { 
    -ms-text-size-adjust: 100%; 
    -webkit-text-size-adjust: 100%; 
    -moz-osx-font-smoothing: grayscale; 
    -webkit-font-smoothing: antialiased; 
    box-sizing: border-box; 
    word-spacing: 1px;
} 

nav { 
    display: block;
} 

.navbar { 
    padding: .5rem 1rem; 
    position: relative;
} 

.navbar { 
    align-items: center; 
    display: flex; 
    flex-wrap: wrap; 
    justify-content: space-between;
} 

@media (min-width: 768px){ 
  .navbar-expand-md { 
    flex-flow: row nowrap; 
    justify-content: flex-start;
  } 
}     

.bg-info { 
    background-color: #17a2b8!important;
} 

.fixed-top { 
    top: 0;
} 

.fixed-top { 
    left: 0; 
    position: fixed; 
    right: 0; 
    z-index: 1030;
} 

.navbar { 
    background-color: #fff; 
    box-shadow: 0 1px 8px 0 rgba(0,0,0,.1);
} 

.navbar { 
    background-color: #fff!important; 
    box-shadow: 0 1px 8px 0 rgba(0,0,0,.1);
} 

#mainNavbar { 
    z-index: 10000;
} 

#mainNavbar { 
    background-color: #fff!important;
} 

*,:after,:before { 
    box-sizing: border-box;
} 

*,:after,:before { 
    box-sizing: border-box; 
    margin: 0;
} 

.container { 
    margin-left: auto; 
    margin-right: auto; 
    padding-left: 15px; 
    padding-right: 15px; 
    width: 100%;
} 

@media (min-width: 576px){ 
  .container { 
    max-width: 540px;
  } 
}     

@media (min-width: 768px){ 
  .container { 
    max-width: 720px;
  } 
}     

@media (min-width: 992px){ 
  .container { 
    max-width: 960px;
  } 
}     

@media (min-width: 1200px){ 
  .container { 
    max-width: 1140px;
  } 
}     

.container { 
    min-height: 50px!important;
} 

.container { 
    align-items: top; 
    display: flex; 
    justify-content: flex-start; 
    margin: 0 auto; 
    max-width: 1140px; 
    min-height: 100vh; 
    text-align: center;
} 

.navbar .container  { 
    align-items: center; 
    display: flex; 
    flex-wrap: wrap; 
    justify-content: space-between;
} 

@media (min-width: 768px){ 
  .navbar-expand-md > .container { 
    flex-wrap: nowrap;
  } 

  .navbar-expand-md > .container, .navbar-expand-md > .container-fluid, .navbar-expand-md > .container-lg, .navbar-expand-md > .container-md, .navbar-expand-md > .container-sm, .navbar-expand-md > .container-xl { 
    flex-wrap: nowrap;
  } 
}     

.container-fluid { 
    margin-left: auto; 
    margin-right: auto; 
    padding-left: 15px; 
    padding-right: 15px; 
    width: 100%;
} 

.navbar .container-fluid  { 
    align-items: center; 
    display: flex; 
    flex-wrap: wrap; 
    justify-content: space-between;
} 

a { 
    background-color: transparent; 
    color: #007bff; 
    -webkit-text-decoration: none; 
    text-decoration: none;
} 

a { 
    color: #3490dc; 
    cursor: pointer;
} 

.navbar-brand { 
    display: inline-block; 
    font-size: 1.25rem; 
    line-height: inherit; 
    margin-right: 1rem; 
    padding-bottom: .3125rem; 
    padding-top: .3125rem; 
    white-space: nowrap;
    color:rgb(255, 127, 80) !important;
} 

.navbar-dark .navbar-brand  { 
    color: #fff;
} 

a:hover { 
    color: #0056b3; 
    -webkit-text-decoration: underline; 
    text-decoration: underline;
} 

a:hover { 
    color:#00684A;
} 

.navbar-brand:hover { 
    -webkit-text-decoration: none; 
    text-decoration: none;
} 

.navbar-dark .navbar-brand , .navbar-dark .navbar-brand:hover { 
    color: #fff;
} 

button { 
    border-radius: 0;
} 

button { 
    font-family: inherit; 
    font-size: inherit; 
    line-height: inherit; 
    margin: 0;
} 

button { 
    overflow: visible;
} 

button { 
    text-transform: none;
} 

[type="button"],button { 
    -webkit-appearance: button;
} 

.navbar-toggler { 
    background-color: transparent; 
    border: 1px solid transparent; 
    border-radius: .25rem; 
    font-size: 1.25rem; 
    line-height: 1; 
    padding: .25rem .75rem;
} 

[type="button"]:not(:disabled),button:not(:disabled) { 
    cursor: pointer;
} 

@media (min-width: 768px){ 
  .navbar-expand-md .navbar-toggler  { 
    display: none;
  } 
}     

.navbar-dark .navbar-toggler  { 
    border-color: hsla(0,0%,100%,.1); 
    color: hsla(0,0%,100%,.5);
} 

.navbar-toggler:hover { 
    -webkit-text-decoration: none; 
    text-decoration: none;
} 

.navbar-collapse { 
    align-items: center; 
    flex-basis: 100%; 
    flex-grow: 1;
} 

.collapse:not(.show) { 
    display: none;
} 

@media (min-width: 768px){ 
  .navbar-expand-md .navbar-collapse  { 
    display: flex!important; 
    flex-basis: auto;
  } 
}     

.navbar-dark .navbar-brand, .navbar-dark .navbar-brand:focus, .navbar-dark .navbar-brand:hover { 
    color: #fff;
} 

img { 
    border-style: none;
} 

img { 
    vertical-align: middle;
} 

svg { 
    vertical-align: middle;
} 

svg { 
    overflow: hidden;
} 

ul { 
    margin-bottom: 1rem;
} 

ul { 
    margin-top: 0;
} 

.navbar-nav { 
    display: flex; 
    flex-direction: column; 
    list-style: none; 
    margin-bottom: 0; 
    padding-left: 0;
} 

.ml-auto { 
    margin-left: auto!important;
} 

@media (min-width: 768px){ 
  .navbar-expand-md .navbar-nav  { 
    flex-direction: row;
  } 
}     

.uppercase { 
    text-transform: uppercase;
} 

.font-weight-bold { 
    font-weight: 700!important;
} 

.nav-link { 
    display: block; 
    padding: .5rem 1rem;
} 

.nav-link { 
    font-size: 16px!important;
} 

.navbar-nav .nav-link  { 
    padding-left: 0; 
    padding-right: 0;
} 

.navbar .nav-link  { 
    font-size: 14px;
} 

@media (min-width: 768px){ 
  .navbar-expand-md .navbar-nav .nav-link  { 
    padding-left: .5rem; 
    padding-right: .5rem;
  } 
}     

.navbar-dark .navbar-nav .nav-link  { 
    color: hsla(0,0%,100%,.5);
} 

.navbar .nav-item .nav-link  { 
    font-size: 14px;
} 

.navbar .nav-item .nav-link  { 
    color: #666!important;
} 

.nav-link:hover { 
    -webkit-text-decoration: none; 
    text-decoration: none;
    
} 

 .navbar-dark .navbar-nav .nav-link:hover { 
    color: hsla(0,0%,100%,.75);
} 

.navbar .nav-item .nav-link:hover { 
    color:#666!important;
    color:rgb(255, 127, 80) !important;
} 

.navbar-dark .navbar-nav .nav-link:focus, .navbar-dark .navbar-nav .nav-link:hover { 
    border: 0;
} 

.navbar-dark .navbar-nav .nav-link:focus, .navbar-dark .navbar-nav .nav-link:hover { 
    color: hsla(0,0%,100%,.75);
} 


/* These were inline style tags. Uses id+class to override almost everything */
#style-zWt8l.style-zWt8l {  
   overflow-anchor: none;  
    border: 1px solid rgb(102, 102, 102) !important;  
} 
@keyframes fadeInDown {
    0% {
      opacity: 0;
      transform: translateY(-20px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  } 
#nav-collapse.show {  
   display: show ;  
   animation: fadeInDown 0.5s ease-out forwards;
}


